.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* background-color: rgb(242, 242, 242); */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
/* h1{
  margin-bottom: 40px;
} */
.input {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.each_box {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  /* border: 2px solid red; */
  border-radius: 8px;
  padding: 8px;
  width: 30%;
  position: relative;
  margin: 8px;

  background: radial-gradient(
    circle,
    rgba(172, 188, 216, 1) 7%,
    rgba(175, 189, 214, 1) 46%,
    rgba(193, 195, 199, 1) 77%,
    rgba(147, 193, 222, 1) 100%
  );
}
input {
  /* margin: auto;
   */
  position: relative;
  /* right: 0;
    */
  left: 100px;
  height: 20px;
}
.output {
  font-size: 22px;
  margin-top: 10px;
}
h1 {
  margin: auto;
  display: block;
  width: 100%;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 39px;
}

.outbox_box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
button {
  font-size: 15px;
  padding: 4px;
  padding-left: 8px;
  padding-right: 8px;
  border-radius: 4px;
  cursor: pointer;
  /* margin-bottom: 20px; */
}

button:hover {
  font-size: 17px;
  background-color: grey;
}

label {
  position: absolute;
  left: 70px;
  font-weight: 700;
}

button {
  margin-top: 30px;
}
label {
  display: inline;
}
input {
  border: 1px solid gray;
  border-radius: 3px;
}
